const target = ["java", "py", "c"];
const curTarget = (() => {
  let num = 0;
  const next = () => {
    num += 1;
    return num % 3;
  };
  return next;
})();

window.setInterval(async () => {
  state.target = target[curTarget()];
  state.searchResult = (await getLanguage(state.target)).map((el, idx) => {
    return { text: el, suggested: idx === 0 ? true : false };
  });
  console.log(state);
  render();
}, 3000);

render();
